import React from 'react'
import Item from 'App/Item/Item'

const items = ['one', 'two', 'three']

function Subgroup({ title }) {
  return (
    <li className="subgroup">
      <h4>{title}</h4>
      <ul>
        {items.map((title, i) => (
          <Item key={i} title={title} />
        ))}
      </ul>
    </li>
  )
}

export default Subgroup
