import React from 'react'
import Subgroup from './Subgroup/Subgroup'
import { lightenColor } from 'utils'

const subgroups = ['subgroup A', 'subgroup B', 'subgroup C']

function Group({ title, color }) {
  return (
    <li className="group">
      <div
        className="group-header"
        style={{
          background: `linear-gradient(
            90deg,
            ${lightenColor(color, 70)} 0%,
            #${color} 100%
          )`
        }}
      >
        <h3>{title}</h3>
        <h5>Subtitle</h5>
      </div>

      <ul>
        {subgroups.map((title, i) => (
          <Subgroup key={i} title={title} />
        ))}
      </ul>

      <div className="drag-here">+ Drag Name Here</div>
    </li>
  )
}

export default Group
