import React from 'react'
import './App.sass'
import Sidebar from './Sidebar/Sidebar'
import Group from './Group/Group'

const groups = ['Group t', 'Group Title', 'Group Title']
const colors = ['f2742c', '4729e4', '419621']

function App({}) {
  return (
    <>
      <Sidebar />

      <div id="content">
        <ul className="groups">
          {groups.map((title, i) => (
            <Group key={i} title={title} color={colors[i]} />
          ))}

          <li className="group">
            <div className="group-header add-new">+ Add New Title</div>
          </li>
        </ul>
      </div>
    </>
  )
}

export default App
